import {Component, Inject, OnInit} from '@angular/core';
import {ElementRef, ViewChild} from '@angular/core';
import {MdDialog, MdDialogRef, MD_DIALOG_DATA} from '@angular/material';
import {DataSource} from '@angular/cdk/collections';
import {Observable} from 'rxjs/Observable';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/of';
import {Http} from '@angular/http';
import {config} from '../../app.config';
import {NgForm} from '@angular/forms';
import 'rxjs/add/operator/take';

declare var $: any;

export interface ChefData {
  id?: number;
  user_uid: number;
  average_rating: number;
  is_active: boolean;
  description: string;
}

@Component({
  selector: 'app-chef',
  templateUrl: './chef.component.html',
  styleUrls: ['./chef.component.css']
})
export class ChefComponent implements OnInit {

  defaultChefData: ChefData = {
    user_uid : 0,
    average_rating: 0,
    is_active: true,
    description: ''
  }

  editMode = false;
  model: ChefData = this.defaultChefData;
  //  @ViewChild('filter') filter: ElementRef;

  // dialogRef;
  chefs: ChefData[] = [
    {
      id: 0,
      user_uid: 2,
      average_rating: 6.7,
      is_active: false,
      description: 'opaopaop'
    },
    {
      id: 1,
      user_uid: 2,
      average_rating: 6.7,
      is_active: false,
      description: 'opaopaop'
    }
  ];

  resetModel() {
    this.model = {
      user_uid : 0,
      average_rating: 0,
      is_active: true,
      description: ''
    };
  }

  constructor(private http: Http) {
  }

  chefsObserver = this.http.get(config.api_prefix + 'chef').map(resp => resp.json().result);
  chefsList;

  ngOnInit() {
    // this.chefsObserver.subscribe(
    //   (res) => console.log('result', res)
    // );
    this.updateChefsList();
  }

  updateChefsList() {
    this.chefsObserver.take(1).subscribe(
      (result) => {
        // console.log(response);
        // const json = response.json();
        console.log('updateChefsList', result);
        this.chefsList = result;
        // if (!json.err) {
        //   this.chefsList = json.result;
        // }
      }
    );
  }

  onNewChef() {
    console.log('new chef');
    $('#create-form').modal('toggle');
  }

  onSave( form: NgForm ) {
    console.log('on save');
    console.log(this.model);

    let request = this.http.post(config.api_prefix + 'chef', this.model);
    if (this.editMode) {
      request = this.http.patch(config.api_prefix + 'chef', this.model);
    }

    request.subscribe(
      (result) => {console.log('ok', result); this.updateChefsList() },
      (err) => console.log('err', err)
    );
    this.resetModel();
    form.reset();
    this.editMode = false;
  }

  onDelete(id) {
    console.log('on delete', id);
    this.http.delete(config.api_prefix + 'chef/' + id).subscribe(
      (result) => {console.log('ok', result); this.updateChefsList() },
      (err) => console.log('err', err)
    );
  }

  onEdit(chef) {
    this.editMode = true;
    console.log('on edit');
    this.model = chef;
    $('#create-form').modal('toggle');
  }

  onDishes(id) {
    console.log('on dishes');

  }
}



