import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Params, Router} from '@angular/router';

import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/observable/from';
import 'rxjs/add/observable/of';
import {Http} from '@angular/http';
import {config} from '../../app.config';
import {NgForm} from "@angular/forms";

declare var $: any;

@Component({
  selector: 'app-dish',
  templateUrl: './dish.component.html',
  styleUrls: ['./dish.component.css']
})
export class DishComponent implements OnInit {

  constructor(private route: ActivatedRoute,
              private router: Router,
              private http: Http) {
  }

  dishesObservablse;
  dishesList;
  model;
  editMode = false;
  chefId = -1;

  resetModel() {
    this.model = {
      serviceprovider_spid: this.chefId,
      images_iid: 0,
      name: '',
      available: 1,
      creation: 0,
      lastmodifytime: 0,
      discription: '',
      price: 0,
      foodcatrgory_fcid: 0,
      image_path: ''
    };
  }

  ngOnInit() {
    this.resetModel();

    this.route.params.subscribe(
      (params: Params) => {
        const id = params['id'];
        if (!id) {
          return;
        }
        this.chefId = id;
        this.dishesObservablse = this.http.get( `${config.api_prefix}chef/${id}/dish`).map(resp => resp.json().result);
        this.updateDishesList();
      }
    );
  }

  updateDishesList() {
    this.dishesObservablse.take(1).subscribe(
      (result) => {
        console.log('updateChefsList', result);
        this.dishesList = result;
      }
    );
  }

  onNewDish() {
    $('#create-form').modal('toggle');
  }

  onSave( form: NgForm ) {
    console.log('on save');
    console.log(this.model);

    let request = this.http.post(`${config.api_prefix}chef/${this.chefId}/dish`, this.model);
    if (this.editMode) {
      request = this.http.patch(`${config.api_prefix}chef/${this.chefId}/dish`, this.model);
    }

    request.subscribe(
      (result) => {console.log('ok', result); this.updateDishesList() },
      (err) => console.log('err', err)
    );
    this.resetModel();
    form.reset();
    this.editMode = false;
  }

  onDelete(id) {
    console.log('on delete', id);
    this.http.delete(`${config.api_prefix}chef/${this.chefId}/dish/${id}`).subscribe(
      (result) => {console.log('ok', result); this.updateDishesList()},
      (err) => console.log('err', err)
    );
  }

  onEdit(dish) {
    this.editMode = true;
    console.log('on edit', dish);
    this.model = dish;
    $('#create-form').modal('toggle');
  }
}



